import uniIcons from '../uni-icons/uni-icons.vue';
import { formatDate1 } from '../../common/formatDate/formatDate.js';

export default {
	name: 'UniSteps',
	components: {
		uniIcons
	},
	props: {
		currentIndex : {
			type: Number,
			default : 0
		},
		direction: {
			// 排列方向 row column
			type: String,
			default: 'row'
		},
		activeColor: {
			// 激活状态颜色
			type: String,
			// default: '#1aad19'
			default: '#5DB793'
		},
		deactiveColor: {
			// 未激活状态颜色
			type: String,
			default: '#999999'
		},
		shopInfoColor: {
			type: String,
			default: '#999999'
		},
		active: {
			// 当前步骤
			type: Number,
			default: 0
		},
		options: {
			type: Array,
			default () {
				return []
			}
		} // 数据
	},
	data() {
		return {}
	},
	
	methods: {
		// 图片预览
		handlePreview(index) {
			let urls = [];
			this.options.forEach((item,i) => {
				urls = item.produceImgList.map(v => v.imgUrl);
				urls.forEach((item,i) => {
					if(!item) {
						urls[i] = this.$api.defaultImg()
					}
				})
			})				
			this.$api.preViewImg(index,urls);
		}
	},
	
	//过滤器
	filters: {
		formatDate1
	}
}
